import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { UtilsProvider } from '../../providers/utils/utils';
import { HttpProvider } from '../../providers/http/http';
import { Storage } from '@ionic/storage';
import { CommandsProvider } from '../../providers/commands/commands';

import * as moment from 'moment';


@IonicPage()
@Component({
  selector: 'page-filtro',
  templateUrl: 'filtro.html',
})
export class FiltroPage {
 
  //hasta la linea 29 pertenece al calendario
  eventSource = [];
  viewTitle: string;
  selectedDay = new Date();
 
  calendar = {
    mode: 'month',
    currentDate: new Date()
  };

  CA_FECINI: string ;
  CA_FECFIN: string ;
  dias: any;
  public verCALENDARIO: boolean = false;
  public verCALENDARIOFIN: boolean = false;

  public flagFiltro: number;
  
  public check : Array<string>;
  public message: string;
  public checkSelected: string;
  public fecha1: Date;
  public fecha2: Date;
  public checkNumber :string;
  public status :string;       
  public date :string;                   
  public amount :string;                 
  public recipient :string;
  public numCuenta: number;
  public tipoCuenta: number;
  public listaMovementCheck : string[];
  public CA_KEYWORD: string;
  public verKEYWORD: boolean = true;

  public verTablaDetalleAccount: boolean = false;
  public listaAccountMvtos: string[] = [];   
  
  //datos de la tarjeta seleccionada
  public creditCardSelected: string; //Esta es donde se obtiene la variable de sesion
  public nombreTarjeta: string;
  public numeroTarjeta: string;
  public verTablaDetalleTarjetaCredito: boolean = false;
  public verTablaDetalleCheques: boolean = false

  constructor(public navCtrl: NavController, public navParams: NavParams, public translate: TranslateService, private alertCtrl: AlertController, 
              public utils: UtilsProvider, public http: HttpProvider, private storage: Storage, public command: CommandsProvider, private modalCtrl: ModalController) {
    
    //valor del tag del cheque seleccionado
    this.flagFiltro =  this.navParams.get("flagFiltro");

    //obtención del Numero y tipo de cuenta del productOverView para filtro de movimientos
    this.storage.get('noCuenta').then((val) => {  
      this.numCuenta = val;
    });
    this.storage.get('tipoCuenta').then((val) => {  
      this.tipoCuenta = val;
    });

    //obtencion de los datos de la tarjeta de credito seleccionada en el productOverView
    this.storage.get('creditCardSelected').then((val) => {  
      this.creditCardSelected = val;
    });

  }

  ionViewDidLoad(){
    //Metodo para mostrar o no el keyword
    this.filtrarCheque(this.flagFiltro);  
  }

  ionViewDidEnter(){
    this.filtrarCheque(this.flagFiltro);
    this.storage.get('checkSelected').then((val) => {          
      this.checkSelected = val;
     });
  }

  //Metodo para validar fecha
  public validate():boolean{
    
   //this.fecha1 = new Date(this.CA_FECINI);
   //this.fecha2 = new Date(this.CA_FECFIN);
   
   if(this.CA_FECINI !== undefined && this.CA_FECINI !== null && this.CA_FECINI !== '' && 
      this.CA_FECFIN !== undefined && this.CA_FECFIN !== null && this.CA_FECFIN !== '' ) {

        this.CA_FECINI = this.CA_FECINI.replace(/\//g,'');
        this.CA_FECFIN = this.CA_FECFIN.replace(/\//g,'');

        var diasDife = this.fecha2.getTime() - this.fecha1.getTime();
         this.dias = Math.round(diasDife/(1000*60*60*24));
        
          if(this.dias > 60){
            this.utils.alertCampos('Filtro.alertDias')+this.dias;
            return false;
          }
          else {
            return true;
          }
    }
    else{
       this.utils.alertCampos('Filtro.alertValidated');
    }
    
    return true;

  }

  buscar() {
    this.verCALENDARIO = false;
    this.verCALENDARIOFIN = false;
    var acceso = this.filtrarCheque(this.flagFiltro);
    
    if(acceso == 1){
        if(this.validate()){
          this.loadChecks(); //consulta de movimientos de las cheques
        }
    }
    else if (acceso == 2){
              if(this.validate()){
               this.loadMovementAccount(); //consulta de movimientos de las cuentas
              }
    } 
    else if (acceso == 3) {
              if (this.validate()) {
               this.loadMovementCreditCard(); //consulta de movimientos de las tarjetas de credito       
              }
    }
   
  }

  //metodo para filtar la información del cheque
  public filtrarCheque(acceso): number{

    if(acceso == 1){
      //this.ocultarKeyword();
      this.verKEYWORD = false;
      acceso = 1;
    }
    else if(acceso == 2) {
      //this.mostrarKeyword();
      this.verKEYWORD = true;
      acceso = 2;
    }
    else if(acceso == 3) {
      //this.mostrarKeyword();
      this.verKEYWORD = true;
      acceso = 3;
    }
    return acceso; 
  }

  //Metodo Cargue de búsqueda por cheque seleccionado y datos del filtro.
  private loadChecks() { 
    
    this.verTablaDetalleCheques = true;
    //var cmd = "E2LSCR12A";
    var command :string[] =  this.utils.getCommands(this.command.E2LSCR12A);
    
    var account = this.checkSelected; //cheque seleccionado
    //this.CA_FECINI='03242010';
    //this.CA_FECFIN='04052018';
    this.CA_FECINI = this.CA_FECINI;
    this.CA_FECFIN = this.CA_FECFIN;

    if(this.validate()){
      var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW='+ 
      this.utils.separator +'Enviar'+ this.utils.separator +  this.utils.separator + command[0] + this.utils.separator +'0'+this.utils.separator+
      this.utils.separator + '&PAR='+ account +this.utils.separator + this.CA_FECINI + this.utils.separator + this.CA_FECFIN + this.utils.separator;

      this.http.callServer(URL).then(res => {      
      this.message = res.message;

        if (this.message != '' && typeof this.message!=="undefined") {
          this.utils.alertServidor(this.message);
        } else {
          this.listaMovementCheck = res.lista2;         
        }
      },
        error => {
          console.log(error);
        });
    }
  }

   //Metodo Cargue de búsqueda de movimientos Cuentas. (productOverView -> account -> movimientos)
   private loadMovementAccount() { 

    this.verTablaDetalleAccount = true; 
    this.verTablaDetalleTarjetaCredito = false;
    //var cmd = "E2LSCR210";
    var command :string[] =  this.utils.getCommands(this.command.E2LSCR210);
    var account = this.numCuenta + "|"+  this.tipoCuenta;
    
    var key = (this.CA_KEYWORD) == undefined ? "" : this.CA_KEYWORD.toUpperCase() ;
    
    //cuenta y el tipo de cuenta
    //if(this.validate()){
    if(true){
      // mes dia año
      //this.CA_FECINI='03242010';
      //this.CA_FECFIN='04052018';
      this.CA_FECINI = this.CA_FECINI;
      this.CA_FECFIN = this.CA_FECFIN;

      var tipoTransaccion: string = "";
      var amountRange1: string = "";
      var amountRange2: string = "";
      var checkREF: string = "";
      var checkREFRange1: string = "";
      var checkREFRange2: string = "";
      var campo7: string = "";
      var campo8: string = "";
      var campo9: string = "";
      var campo10: string = "";


      var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW='+ 
      this.utils.separator +'Enviar'+ this.utils.separator +  this.utils.separator + 'E2SECR61P' + this.utils.separator +'0'+this.utils.separator+
      this.utils.separator + '&PAR='+ account + this.utils.separator +this.tipoCuenta+this.utils.separator+ key + this.utils.separator +
       this.CA_FECINI+this.utils.separator+ this.CA_FECFIN+ this.utils.separator +
        tipoTransaccion + this.utils.separator + amountRange1 + this.utils.separator + amountRange2 + this.utils.separator + checkREF +
        this.utils.separator + checkREFRange1 + this.utils.separator + checkREFRange2 + this.utils.separator + campo7 +
        this.utils.separator + campo8 + this.utils.separator + campo9 + this.utils.separator + campo10 +this.utils.separator;

      this.http.callServer(URL).then(res => {      
      this.message = res.message;

        if (this.message != '' && typeof this.message!=="undefined") {
          this.utils.alertServidor(this.message);
        } else {
          this.listaAccountMvtos = res.lista1; 
          var lista = this.listaAccountMvtos.splice(0,1);      
        }
      },
        error => {
          console.log(error);
        });
    }
  }

  //Metodo Cargue de búsqueda de movimientos Tarjetas de credito. (productOverView -> credit card -> movimientos)
  private loadMovementCreditCard() { 

    this.verTablaDetalleTarjetaCredito = true;
    this.verTablaDetalleAccount = false; 
    
    //var cmd = "E2LSCR210";
    var command :string[] =  this.utils.getCommands(this.command.E2LSCR89L);
    
    this.nombreTarjeta = this.creditCardSelected[2];
    this.numeroTarjeta = this.creditCardSelected[0]

    //cuenta y el tipo de cuenta
    //if(this.validate()){
    if(true){
      // mes dia año
      //this.CA_FECINI='03242010';
      //this.CA_FECFIN='04052018';
      this.CA_FECINI = this.CA_FECINI;
      this.CA_FECFIN = this.CA_FECFIN;
      var pagina1: string = "";
      
      var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW='+ 
      this.utils.separator +'Enviar'+ this.utils.separator +  this.utils.separator + 'E2SECR61P' + this.utils.separator +'0'+this.utils.separator+
      this.utils.separator + '&PAR='+ this.nombreTarjeta + this.utils.separator +this.numeroTarjeta +this.utils.separator+ 
       this.CA_FECINI+this.utils.separator+ this.CA_FECFIN+ this.utils.separator + pagina1 + this.utils.separator + pagina1 + this.utils.separator + 
      "D"+ this.utils.separator + this.utils.separator;

      this.http.callServer(URL).then(res => {      
      this.message = res.message;

        if (this.message != '' && typeof this.message!=="undefined") {
          this.utils.alertServidor(this.message);
        } else {
          this.listaAccountMvtos = res.lista2; 
          //var lista = this.listaAccountMvtos.splice(0,1);
          //alert(lista);       
        }
      },
        error => {
          console.log(error);
        });
    }
  }


  back(){
    if(this.flagFiltro == 1){
      this.navCtrl.push("CheckStatePage");
    }
    else{
      this.navCtrl.push("ProductOverViewPage");
    }
    
  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }


  //  METODOS PARA EL CALENDARIO

  //Carga el titulo del mes
   onViewTitleChanged(title) {
    //alert("titulo "+title)
    this.viewTitle = title;
   
  }

  onEventSelected(event) { }

  //obtiene la fecha seleccionada
  onTimeSelected(ev, paso) {
    
    this.selectedDay = ev.selectedTime;
    var diaACTUAL = ( parseInt(this.selectedDay.getDate().toString()) <= 9 ) ? "0"+this.selectedDay.getDate().toString() : this.selectedDay.getDate().toString();
    var mesACTUAL = ( (this.selectedDay.getMonth()+1)  <= 9 ) ? "0"+(this.selectedDay.getMonth()+1).toString() : (this.selectedDay.getMonth()+1).toString() ;
    var añoACTUAL = this.selectedDay.getFullYear().toString();

    if(paso == 1){
      this.CA_FECINI = mesACTUAL+diaACTUAL+añoACTUAL;      
      this.fecha1 = new Date(mesACTUAL+"/"+diaACTUAL+"/"+añoACTUAL);
   
    }
    else if(paso == 2){
      this.CA_FECFIN = mesACTUAL+diaACTUAL+añoACTUAL;      
      this.fecha2 = new Date(mesACTUAL+"/"+diaACTUAL+"/"+añoACTUAL);
    }
                 
    
  }

  abrirCalendarINICIO(){
    //variables para ver o no el calendario
    this.verCALENDARIO = true;
    this.verCALENDARIOFIN = false;
     
    this.verTablaDetalleCheques = false;  //Esconde las tablas de detalles de cheques
    this.verTablaDetalleAccount = false;  //Esconde las tablas de detalles de las cuentas
    this.verTablaDetalleTarjetaCredito = false;  //Esconde las tablas de detalles de las tarjetas de credito
  }

  abrirCalendarFIN(){
    this.verCALENDARIO = false;
    this.verCALENDARIOFIN = true;

    this.verTablaDetalleCheques = false;  //Esconde las tablas de detalles de cheques
    this.verTablaDetalleAccount = false;  //Esconde las tablas de detalles de las cuentas
    this.verTablaDetalleTarjetaCredito = false;  //Esconde las tablas de detalles de las tarjetas de credito
  }

}



